.price-top{
    text-align: center;
}
.price-top h2{
    font-size: 2.5rem;
    margin-bottom: 20px;
}
.price-top p{
    font-size: 1.1rem;
    color: var(--heading-color);
    line-height: 28px;
}
.price-wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    margin-bottom: 30px;
    column-gap: 2rem;
}
.pricing-item{
    box-shadow: rgba(0,0,0,0.15)0px 5px 15px 0px;
    border-radius: 5px;
    width: 33%;
    background: #fff;
}
.pricing-item:hover{
    transform: translateY(-10px);
    box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
}
.pricingcard-top{
    padding: 30px;
    border-bottom: 1px solid #d3cef2;
    text-align: center;
}
.pricingcard-top h2{
    font-size: 1.5rem;
    margin-bottom: 0.9rem;
}
.pricingcard-top .pricing{
    font-size: 1.4rem;
}
.pricingcard-top .pricing span{
    font-size: 1rem;
    color: #666;
}
.pricing-item .services{
    padding: 30px;
}
.pricing-item .services ul li{
    display: flex;
    align-items: center;
    column-gap: 1.5rem;
    columns: #666;
    line-height: 28px;
}
.pricing-item.services ul li span li{
    font-size: 0.7rem;
}
.services{
    text-align: center;
}
.services button{
    margin-top: 50px;
}
.pricing-item-02.pricingcard-top{
    background: var(--primary-color);
    border-radius: 5px 5px 0px 0px;
}
.pricing-item-02.pricingcard-top h2{
    color: #fff;
    font-weight: 500;
}
.pricing-item-02.pricingcard-top h2 span{
    color: rgba(255,.255,.255, .0.788);
}
.pricing-item-02.services ul li span i{
    color: var(--primary-color);
}